const skills = [
  "JavaScript", "TypeScript", "HTML/CSS", "SQL", "MongoDB", "Node.js", "Next.js", "Angular"
];

const competencies = [
  "Agile Methodologies", "Restful API", "Performance Optimization", "Containerization"
];

export default function Skills() {
  return (
    <section className="py-20 border-t">
      <div className="grid md:grid-cols-2 gap-16">
        <div>
          <h3 className="text-2xl font-bold mb-8 italic">Technical Skills [cite: 14]</h3>
          <div className="flex flex-wrap gap-3">
            {skills.map(s => (
              <span key={s} className="px-5 py-2 bg-white border border-slate-200 rounded-xl font-medium shadow-sm">{s}</span>
            ))}
          </div>
        </div>
        <div>
          <h3 className="text-2xl font-bold mb-8 italic">Core Competencies [cite: 20]</h3>
          <ul className="grid grid-cols-2 gap-4">
            {competencies.map(c => (
              <li key={c} className="text-slate-600 flex items-center gap-2 text-sm">
                <div className="w-1.5 h-1.5 bg-blue-600 rounded-full" /> {c}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </section>
  );
}