import { ExternalLink } from "lucide-react";

const projectData = [
  {
    title: "JOB-HIVE",
    desc: "A subscription-based remote job searching platform with Razorpay integration and dynamic job filtering.",
    tech: ["Next.js", "TypeScript", "Tailwind", "MongoDB", "Razorpay"],
  },
  {
    title: "BYTECODE",
    desc: "Full-stack blog application featuring user authentication, CRUD for posts, and Cloudinary image uploads.",
    tech: ["Next.js 15", "MongoDB Atlas", "Cloudinary", "JavaScript", "Tailwind"],
  },
  {
    title: "DIZIT SOLUTION",
    desc: "A web platform for booking AC repair services that directly notifies mechanics via WhatsApp API.",
    tech: ["Next.js", "TypeScript", "Tailwind CSS", "WhatsApp API"],
  }
];

export default function Projects() {
  return (
    <section id="projects" className="py-24">
      <h2 className="text-sm font-bold text-blue-600 uppercase tracking-[0.3em] mb-8 text-center">Selected Projects</h2>
      <div className="space-y-6 max-w-3xl mx-auto">
        {projectData.map((project, i) => (
          <div key={i} className="group p-8 bg-white/40 backdrop-blur-md border border-white/60 rounded-3xl hover:bg-white/60 transition-all duration-500 shadow-sm hover:shadow-xl">
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
              <div>
                <h3 className="text-xl font-bold text-slate-900 mb-2">{project.title}</h3>
                <p className="text-slate-600 text-sm max-w-md">{project.desc}</p>
              </div>
              <div className="flex flex-wrap gap-2">
                {project.tech.map((t) => (
                  <span key={t} className="px-3 py-1 bg-slate-200/50 text-slate-700 text-[10px] font-bold rounded-lg uppercase tracking-wider">{t}</span>
                ))}
              </div>
            </div>
            <div className="mt-6 flex items-center gap-2 text-xs font-bold text-slate-400 group-hover:text-blue-600 transition-colors">
              <span>View Case Study</span>
              <ExternalLink size={14} />
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}